body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
div {box-sizing:border-box;}
h4 {margin:0;}
.black-nav {display:flex; background:#000; width:100%; padding:20px; color:#fff;}

.list {padding:20px; border-bottom:1px solid grey; text-align:left;}
.list span {margin-left:10px;}

.modal {margin-top:20px; padding:20px; background:#eee; text-align:left;}